.markdown {
  display: block;
  word-wrap: break-word;
  color: #333;
  line-height: 1.7;
  text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
}

.markdown blockquote {
  margin: 0;
  margin-bottom: 0.85em;
  padding: 0 15px;
  color: #858585;
  border-left: 4px solid #e5e5e5;
}

.markdown blockquote {
  margin: 0;
  margin-bottom: 0.85em;
  padding: 0 15px;
  color: #858585;
  border-left: 4px solid #e5e5e5;
}

.markdown pre {
  overflow: auto;
  word-wrap: normal;
  margin: 0;
  padding: 0.85em 1em;
  margin-bottom: 1.275em;
  background: #f7f7f7;
}
